/* Tooltip text */
.area-button-recorder .tooltiptext {
  visibility: hidden;
  width: 130px;
  background-color: #555;
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 30px;
  font-family: Rawline;
  font-size: 12px;

  /* Posição do tooltip text */
  position: absolute;
  bottom: 110%;
  left: -30px;

  /* transição do tooltip */
  opacity: 1;
  transition-duration: 2s;
}

/* Mostra o tooltip text quando o mouse passa por cima do tooltip container */
.area-button-recorder:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}
