.title-recorder {
  font-family: Rawline;
  text-align: center;
  height: 100%;
}

.recorder-area {
  display: flex;
  justify-content: center;
  align-content: center;
}

.recorder-area .history-recorder {
  position: absolute;
  margin-left: 180px;
  margin-top: 24px;
}

.fixed-area-recorder {
  position: fixed;
  bottom: 35px;
  width: 100%;
}

.area-button-recorder p {
  font-family: Rawline;
  font-size: 12px;
  margin: 0px;
  text-align: center;
}

.main-area-recorder {
  margin: auto;
  height: 75%;
  padding: 20px;
  border: none;
  background: none;
}

.main-area-recorder .bg-img {
  position: absolute;
  margin: auto;
  top: -70px;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
}

.main-area-recorder .bg-img.bg-opacity {
  opacity: 0.6;
}

.main-area-recorder .title-area {
  font-family: Rawline;
  display: inline-flex;
  width: 100%;
  color: #1b1b1b;
  font-size: 14px;
}

.main-area-recorder .logo-icon {
  margin-right: 7px;
}

.main-area-recorder .title {
  display: inline-flex;
  width: 100%;
}

.main-area-recorder .container-outputs {
  border: 1px solid #e0e0e0;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 5px 8px;
  display: flex;
  flex-flow: wrap;
  /* overflow: hidden;
  overflow-x: auto; */
  /* white-space: nowrap; */
}

.main-area-recorder .container-outputs span {
  font-family: Rawline;
  background: #ededed;
  color: #565656;
  font-size: 14px;
  line-height: 17.5px;
  border-radius: 48px;
  padding: 6px 12px;
  margin: 4px 5px;
  cursor: pointer;
  /* display: flex;
  justify-content: flex-start; */
  /* margin: 20px; */
  /* display: inline-block; */
}
.area-button-recorder {
  position: relative;
  display: inline-block;
}

.main-area-recorder-button-none {
  border: 0;
  background: none;
}

.main-area-recorder-button-none.container-output {
  width: 100%;
}

.customization-menu {
  background: white;
  display: flex;
  flex-direction: row;
  padding: 16px;
}

.customization-menu-principal {
}

.customization-list-colors {
  display: flex;
  flex-direction: row;
  overflow-x: scroll;
  padding: 16px;
}

.customization-item-colors {
  min-width: 46px;
  min-height: 46px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 1;
  border-radius: 50%;
  border: 3px solid;
  margin-right: 12px;
}

.customization-button-colors {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  display: inline-block;
}

.customization-save {
  border-radius: 24px;
  background-color: #1447a6;
  border-radius: 24px;
  text-transform: none;
  letter-spacing: 0px;
  align-self: center;
  padding: 8px 0;
  width: 94px;
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 145%;
  color: white;
  /* or 23px */

  text-align: center;
}

.customization-all-save {
  background: white;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 16px;
}

.customization-button-menu {
  background: transparent;
  border: none;
  display: flex;
  flex: 1 1;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-bottom-width: 2px;
  border-bottom-style: solid;
  padding-bottom: 3px;
  margin: 0 !important;
}

.player-container {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  background: white;
}

.player-content {
  display: flex;
  flex: 1 1;
  flex-shrink: inherit;
}

.customization-reset {
  border-radius: 24px;
  background-color: transparent;
  border-radius: 24px;
  text-transform: none;
  letter-spacing: 0px;
  align-self: center;
  padding: 8px 0;
  border: 1px solid #e0e0e0;
  width: 94px;

  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 145%;
  color: #4e4e4e;
  /* or 23px */

  text-align: center;
  margin-right: 16px;
}

.tutorial-container {
  display: flex;
  flex-direction: column;
  flex: 1 1;
  width: 100%;
  justify-content: flex-start;

  margin: 0;
  margin-left: 16px;
}

.tutorial-header {
  display: flex;
  flex-direction: row;
  align-items: center;

  margin-top: 16px;
  margin-left: 16px;
}
.tutorial-header-text {
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 125%;
  /* identical to box height, or 22px */


  /* Primary - dark */

  color: #1447a6;
}
.tutorial-tip-container {
  display: flex;
  flex-direction: row;
  align-items: center;

  margin-top: 27px;

  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.tutorial-tip-text {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 16.8px;
  line-height: 120%;
  /* or 20px */

  margin-right: 139px;


  /* Gray */

  color: #4e4e4e;
}

.tutorial-toggle {
  width: 48px;
