.scroll-content {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  position: absolute;
  z-index: 1;
  overflow-x: hidden;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  will-change: scroll-position;
  contain: size style layout;
  display: flex;
  flex-direction: column;
}

body {
  --ion-safe-area-bottom: env(safe-area-inset-bottom);
}

.tutorial-box-shadow {
  position: absolute;
  width: 240px;
  height: 94px;
  bottom: 94px;
  background: #1b1b1b;
  border-radius: 4px;
  margin: auto;
  left: 0;
  right: 0;
  z-index: 3;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.tutorial-box-shadow .button-solid {
  flex: 1;
  background: #1447a6;
  /* border-radius: 50%; */
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 100%;
  /* identical to box height, or 14px */
  border-radius: 46px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 8px 0;
  text-align: center;
  margin: 0 16px 0 4px;

  color: #ffffff;
}

.tutorial-box-shadow .button-outlined {
  flex: 1;
  background: transparent;
  border: 1px solid #4E4E4E;
  box-sizing: border-box;
  /* border-radius: 50%; */
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 100%;
  /* identical to box height, or 14px */
  border-radius: 46px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 8px 0;
  text-align: center;
  margin: 0 4px 0 16px;

  color: #ffffff;
}

.tutorial-box-shadow h1 {
  font-family: Rawline;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 100%;
  margin: 16px 0;
}
